import { Component, OnInit } from '@angular/core';
import {CpanelService} from '../cpanel/services/cpanel.service';

@Component({
  selector: 'app-check',
  templateUrl: './check.component.html',
  styleUrls: ['./check.component.css']
})
export class CheckComponent implements OnInit {

  dataArray = [];
  selectedRow;
  brutto;
  netto;
  difference;
  bigger: boolean;
  smaller: boolean;
  warning: boolean;

  constructor(private cpanelService: CpanelService) { }

  ngOnInit() {

    this.cpanelService.getData().subscribe(
      data => {
        this.dataArray = data.map(item => {
          return {
            $key: item.key,
            ...item.payload.val()
          };
        });
      }
    );
  }

  getSelected(product) {
    this.dataArray.forEach( data =>{
      if (product.target.value == data.product) {
        this.selectedRow = data;
      }
    });
    this.bigger = false;
    this.smaller = false;
    this.warning = false;
    this.brutto = null;
    this.netto = null;
  }

  checkDifference() {
    if (this.brutto > 0 && this.netto > 0 ){
      this.difference = this.brutto - this.netto;
      if (this.difference > this.selectedRow.allow_number) {
        this.bigger = true;
        this.smaller = false;
        this.warning = false;
      } else if (this.difference > 0 && this.difference < this.selectedRow.allow_number){
        this.smaller = true;
        this.bigger = false;
        this.warning = false;
      } else if (this.difference < 0) {
        this.bigger = false;
        this.smaller = false;
        this.warning = true;
      }
    }
  }
}
