import { Component, OnInit } from '@angular/core';
import {CpanelService} from '../cpanel/services/cpanel.service';

@Component({
  selector: 'app-cpanel-list',
  templateUrl: './cpanel-list.component.html',
  styleUrls: ['./cpanel-list.component.css']
})
export class CpanelListComponent implements OnInit {

  constructor(private cpanelService: CpanelService) { }

  dataArray = [];
  succesfullyDeleted: boolean;

  ngOnInit() {

    this.cpanelService.getData().subscribe(
      data => {
        this.dataArray = data.map(item =>{
          return {
            $key: item.key,
            ...item.payload.val()
          };
        });
      }
    );
  }

  onDelete($key) {
    if (confirm('Bu məlumatı silməyə əminsinizmi?')) {
      this.cpanelService.deleteData($key);
      this.succesfullyDeleted = true;
      setTimeout(() => this.succesfullyDeleted = false, 2000);
    }
  }

}

