import { Component, OnInit } from '@angular/core';
import {CpanelService} from './services/cpanel.service';

@Component({
  selector: 'app-cpanel',
  templateUrl: './cpanel.component.html',
  styleUrls: ['./cpanel.component.css']
})
export class CpanelComponent implements OnInit {

  constructor(private cpanelService: CpanelService) { }

  submitted: boolean;
  submmittedSuccesfully: boolean;
  formControls = this.cpanelService.form.controls;
  ngOnInit() {
  }

  onSubmit() {
    this.submitted = true;
    if (this.cpanelService.form.valid) {

      if (this.cpanelService.form.get('$key').value == null) {
        this.cpanelService.insertData(this.cpanelService.form.value);
      } else {
        this.cpanelService.updateData(this.cpanelService.form.value);
      }
    }
    this.submmittedSuccesfully = true;
    setTimeout(() => this.submmittedSuccesfully = false, 2000);
    this.submitted = false;
    this.cpanelService.form.reset();
  }


}
